import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import {BANG, LIST, SYMBOL, NUMBER} from 'zobject/data-types.js';

const ZMessageInformation = ({zmessage, type}) => { 
    let src = '';
    let name = '';
    switch (type) {
    case BANG:
        src = '/zbutton-light.svg';
        name = 'Bang';
        break;
    case NUMBER:
        src = '/znumber-light.svg';
        name = 'Number';
        break;
    default:
        src = '/zmessage-light.svg';
        name = 'Message';
        break;
    }
    return (
        <div className="object-information zmessage-information">
          <img src={src}/>
          <div className="object-name">
            {name}
          </div>
        </div>);
}

const mapStateToProps = (state, props) => {
  return {
      type: props.zmessage.type
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ZMessageInformation);
