import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';

import './zobject.scss';
const ZObjectInformation = ({zobject}) => { 
    let src = zobject.name === 'comment' ? '/zcomment-light.svg': '/zobject-light.svg';
    if (zobject.plugin) {
        src = "/plugin-icon.svg";
    }
    return (<div className="zobject-information object-information">
              <img className="zobject-information-icon" src={src}/>
              <div className="object-name">
                {zobject.plugin ? zobject.plugin.name : zobject.name}
              </div>
            </div>
           );
}

const mapStateToProps = (state, props) => {
  return {
      name: props.zobject.name
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ZObjectInformation);
